.content {
  max-width: 1200px;
  margin: 0 auto;
}
.content .header {
  display: flex;
  padding: var(--lrg-space);
  border-bottom: 1px solid black;
}
.content .header-search {
  flex: 1 1 100%;
  font-size: 20px;
  line-height: 30px;
  border: none;
}
.content .header-search:focus {
  outline: none;
  border-bottom: 1px solid grey;
}
.content .header-clear {
  border: none;
  background-color: white;
}
.content .header-clear:hover {
  cursor: pointer;
}
.content .header-clear:focus {
  outline: none;
}
.content .showList {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  padding: var(--xlrg-space) 0;
}
.content .showList .loader,
.content .showList .noResults {
  display: inline-block;
  font-size: 30px;
  margin: auto;
}
