.showModal {
  top: 0;
  left: 0;
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
}
.showModal .show {
  --footer-height: 45px;
}
.showModal .show-poster {
  max-width: 100vw;
  max-height: calc(100vh - var(--footer-height));
  object-fit: contain;
}
.showModal .show-unavailable {
  padding: var(--lrg-space);
  font-size: 30px;
  background-color: white;
}
.showModal .show-footer {
  display: flex;
  align-items: center;
  justify-content: center;
}
.showModal .show-footer-close {
  margin: var(--med-space) 0;
}
.showModal .show-footer-close:hover {
  cursor: pointer;
}
