.preview {
  --default-preview-width: 300px;
  --image-max-size: 150px;
  display: flex;
  align-items: center;
  flex: 1 0 var(--default-preview-width);
  border: 1px solid black;
  margin: 0 var(--med-space) var(--lrg-space) var(--med-space);
}
.preview:hover {
  cursor: pointer;
  box-shadow: 0 0 5px black;
}
.preview-image {
  flex: 0 0 var(--image-max-size);
  height: var(--image-max-size);
  object-fit: cover;
  border-right: 1px solid black;
}
.preview-default {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 var(--image-max-size);
  height: var(--image-max-size);
  border-right: 1px solid black;
}
.preview-no-image {
  font-size: 36px;
  text-align: center;
}
.preview-text {
  padding: 0 var(--med-space);
}
